import React from "react";
import { ZpopoverButton } from "zerod";
import { Button } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/ZpopoverButton/doc.md";
export default AmdDocHOC(doc, {
	demo1: function() {
		class Myjavascript extends React.PureComponent {
			methods = {
				onCancel: () => {
					this.ZpopoverButtonMethods.closePopover();
				},
			};
			popover = {
				title: <div>popover标题</div>,
				content: (
					<React.Fragment>
						<div>popover内容</div>
						<div className="z-margin-top-20">
							<Button block onClick={this.methods.onCancel}>取消</Button>
						</div>
					</React.Fragment>
				),
				onVisibleChange: (visible) => {
					console.log(visible);
				},
				onGetPopupContainer: function() {
					return document.body;
				},
				exportMethods: (m) => {
					this.ZpopoverButtonMethods = m;
				},
				defaultDisabled: false,
				defaultShow: true,
				placement: "right",
				btnClassName: "z-margin-left-20",
			};
			render() {
				return <ZpopoverButton {...this.popover}>内置按钮</ZpopoverButton>;
			}
		}
		return <Myjavascript />;
	},
});
